.cq-chart-size {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: auto;

    &:after {
        display: none;
    }
    > span {
        flex-grow: 1;
        opacity: 1;
        position: relative;
        cursor: pointer;
        transition: scale .25s;
    }

    .interval-title {
        white-space: nowrap;
        width: auto;
        line-height: 25px;
        display: none; /* TODO */
    }
}



